import { css } from "@emotion/react";
import { graphql } from "gatsby";
import { Title } from "react-head";
import Block from "../../components/block";
import Layout from "../../components/layout";
import { PostData } from "../../components/post-list";
import { postStyle } from "../../lib/post-style";
import PageTransition from 'gatsby-plugin-page-transitions';

export default function PostPage({ data }) {
  const postData: PostData = data.markdownRemark;
  return(
    <PageTransition
      transitionTime={500}
    >
      <Layout>
        <Title>{
          data.markdownRemark.frontmatter.title
            ? `${postData.frontmatter.title} - ${data.site.siteMetadata.title}`
            : data.site.siteMetadata.title
        }</Title>
        <Block>
          {postData.frontmatter.title?
            <h1 css={styles.title}>{postData.frontmatter.title}</h1>
          : null}
          <div
            css={postStyle}
            dangerouslySetInnerHTML={{__html: postData.html}}
          />
        </Block>
        <Block>
          <div
            dangerouslySetInnerHTML={{__html: `
            <div id="lv-container" data-id="city" data-uid="MTAyMC80ODYwNS8yNTA5OQ==">
            <script type="text/javascript">
               (function(d, s) {
                   var j, e = d.getElementsByTagName(s)[0];
            
                   if (typeof LivereTower === 'function') { return; }
            
                   j = d.createElement(s);
                   j.src = 'https://cdn-city.livere.com/js/embed.dist.js';
                   j.async = true;
            
                   e.parentNode.insertBefore(j, e);
               })(document, 'script');
            </script>
            </div>
            `}}
          />
        </Block>
      </Layout>
    </PageTransition>
  )
}

export const pageQuery = graphql`
  query Post($id: String) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: {eq: $id}) {
      html
      frontmatter {
        title
      }
    }
  }
`

const styles = {
  title: css`
    font-size: 35px;
    font-weight: bold;
    margin: 0 0 15px 0;
  `
}